import { TextField } from '@material-ui/core';
import React, { useState } from 'react'
import validator from 'validator';
import swal from 'sweetalert';
import { connect } from 'react-redux';
import CardProduct from './CardProduct';

const Contact = (props) => {
    const { favoriteNails } = props;

    const [formValues, setFormValues] = useState({
        nombre: '',
        email: '',
        direccion: '',
        comuna: '',
        mensaje: '',
    });

    const { nombre, email, direccion, comuna, mensaje } = formValues;

    const handleSubmit = (e) => {
        e.preventDefault();
        if (isFormValid()) {
            swal("¡Datos enviados correctamente!", "A la brevedad nos pondremos en contacto contigo", "success");
            setFormValues({
                nombre: '',
                email: '',
                direccion: '',
                comuna: '',
                mensaje: '',
            })
            setTimeout(function () { window.location.reload() }, 1200);

        }


    }

    const isFormValid = () => {

        if (nombre.trim().length === 0 || direccion.trim().length === 0 || comuna.trim().length === 0 || mensaje.trim().length === 0) {
            swal("Campos vacíos", "Tiene campos sin completar", "error");
            return false;
        } else if (!validator.isEmail(email)) {
            swal("Email inválido", "El correo que ha ingresado es inválido", "error");
            return false;
        }
        return true;
    }

    const handleInputChange = (event) => {
        setFormValues({
            ...formValues,
            [event.target.name]: event.target.value
        })

    }


    return (
        <div className="contact-container">
            <div className="info-title">
                <h2>Formulario <span>de contacto</span></h2>
            </div>
            <div className="contact-form-container">
                <form action='../services/sendMail.php' name="form" onSubmit={handleSubmit}>
                    <TextField
                        onChange={handleInputChange}
                        autoComplete="off"
                        value={nombre}
                        id="standard-helperText"
                        label="Nombre completo"
                        type="text"
                        name="nombre"
                    />
                    <TextField
                        onChange={handleInputChange}
                        autoComplete="off"
                        value={email}
                        id="standard-helperText"
                        label="Email"
                        type="text"
                        name="email"

                    />
                    <TextField
                        onChange={handleInputChange}
                        autoComplete="off"
                        value={direccion}
                        id="standard-helperText"
                        label="Dirección"
                        type="text"
                        name="direccion"

                    />
                    <TextField
                        onChange={handleInputChange}
                        autoComplete="off"
                        value={comuna}
                        id="standard-helperText"
                        label="Comuna"
                        type="text"
                        name="comuna"

                    />
                    <TextField
                        onChange={handleInputChange}
                        autoComplete="off"
                        value={mensaje}
                        id="standard-helperText"
                        label="Mensaje"
                        type="text"
                        name="mensaje"
                    />
                    <button type='submit' class="fill">
                        Enviar
                    </button>
                    <div class="button__horizontal"></div>
                    <div class="button__vertical"></div>

                </form>

                <div>
                    <h1>Lista de pedidos</h1>
                    <div className="order-list">
                        {
                            !favoriteNails.length ?
                                <h3>Lista de pedidos vacías, solo se enviará un mensaje de contacto. Si desea generar un
                                    presupuesto, debe seleccionar los diseños que desee.
                                </h3>
                                : favoriteNails.map(item => (

                                    <CardProduct key={item.id} data={item.data} />

                                ))
                        }
                    </div>
                </div>
            </div>

        </div>
    )
}

const mapStateToProps = (state) => {
    return {
        favoriteNails: state.favoriteNails
    }
}

export default connect(mapStateToProps, null)(Contact);