import React, { useEffect, useState } from 'react';
import { connect } from 'react-redux';
import { setFavorite, deleteFavorite } from '../redux/actions';


const CardProduct = (props) => {
    const { data, favoriteNails } = props;
    const id = props.data.id;

    const [favoriteItem, setFavorite] = useState(false);

    const handleDeleteFavorite = (id) => {
        props.deleteFavorite(id);
        setFavorite(false);
    }

    const handleSetFavorite = () => {
        props.setFavorite({ data });
        setFavorite(true)
    }

    const isFavorite = () => {
        const result = favoriteNails.filter(
            (favoriteNailsItem) => favoriteNailsItem.data.id === id
        );
        if (result.length) setFavorite(true)
    }

    useEffect(() => {
        isFavorite()
    }, [favoriteItem]);

    return (
        <div className={favoriteItem === false ? 'img-card-product' : 'img-card-product border'}>
            <img src={props.data.imgUrl} />
            <p>{props.data.name}</p>
            <p><span>{props.data.price}</span></p>
            {
                favoriteItem ?
                    <button className="save-card button-bg" type="submit" onClick={() => handleDeleteFavorite(id)}>Guardar</button>
                    : <button className="save-card" type="submit" onClick={handleSetFavorite}>Guardar</button>
            }
        </div >
    )
}

const mapStateToProps = (state) => {
    return {
        favoriteNails: state.favoriteNails
    }
}

const mapDispatchToProps = {
    setFavorite,
    deleteFavorite
}

export default connect(mapStateToProps, mapDispatchToProps)(CardProduct)