import React from 'react'
const AboutUs = () => {
    return (
        <div className="info-container">
            <div className="info-title">
                <h2>Sobre <span>nosotros</span></h2>
            </div>
            <div className="info-section-container-about">
                <div className="info-text-about">

                    <p>
                        Hola, soy <span>Javiera</span> una emprendedora de tiempo completo, que busca ir en tú ayuda cuando necesites un cambio de look. La presentación personal vale más que mil palabras, y es por ello que te aseguraré que estarás en buenas manos. He dedicado mucho tiempo al ámbito cosmético, busco brindar la mejor atención profesional que pueda otorgarle a mis clientes. Para esto me encuentro en constante perfeccionamiento, para entregar productos exclusivos de alta calidad. Estoy completamente dispuesta para sacar a relucir tú lado más estiloso e innovarlo con productos de alta gama.</p>
                </div>
                <div className="info-img-about">
                    <img src='https://i.ibb.co/1TYvc2Q/img-aboutus.jpg' alt="Imágen dueña" />
                </div>
            </div>
        </div>
    )
}

export default AboutUs;