const reducer = (state, action) => {
    switch (action.type) {
        case 'SET_FAVORITE':
            return {
                ...state,
                favoriteNails: [...state.favoriteNails, action.payload],
            };

        case 'DELETE_FAVORITE':
            return {
                ...state,
                favoriteNails: state.favoriteNails.filter(
                    (items) => items.data.id !== action.payload
                ),
            };

        default:
            return state
    }
}

export default reducer;