import React from 'react'
import { Link } from 'react-router-dom'
const Main = () => {
    return (
        <div>

            <div class="header-imagen">
            </div>
            <div class="container">
                <div class="initial-info">
                    <h2>PRESS ON NAILS</h2>
                    <p>Bienvenida/o a nuestro sitio web. Aquí podrás conocer todos nuestros productos disponibles
                        y además <span>podrás realizar cotizaciones de los que más te gusten.</span>
                    </p>
                    <button type='button' class="fill">
                        <Link to='/about-us'>
                            Sobre nosotros
                        </Link>
                    </button>
                    <div class="button__horizontal"></div>
                    <div class="button__vertical"></div>
                </div>
                <div class="featured">
                    <h2 class="featured-title">Productos destacados</h2>
                    <hr class="slash-2" />
                    <div class="featured-container-img">
                        <img src='https://i.ibb.co/yg3DFHG/featured-1-f6fd0918.png' alt="Imágen 1" />
                        <img src='https://i.ibb.co/9tZFv9Q/featured-2-57407ae5.png' alt="Imágen 2" />
                        <img src='https://i.ibb.co/Nj1rN4G/featured-3-9fa922e9.png' alt="Imágen 3" />
                        <img src='https://i.ibb.co/sbxYFKJ/featured-4-9a3bf628.png' alt="Imágen 4" />
                    </div>
                </div>
                <div class="more-products">
                    <h2 class=" featured-container-img featured-title">Productos en oferta</h2>
                    <hr class="slash-2" />

                    <div class="more-products-container">
                        <div class="more-products-card">
                            <img src='https://i.ibb.co/yg3DFHG/featured-1-f6fd0918.png' alt="Imágen 1" />
                            <p>$00.000</p>
                        </div>
                        <div class="more-products-card">
                            <img src='https://i.ibb.co/9tZFv9Q/featured-2-57407ae5.png' alt="Imágen 2" />
                            <p>$00.000</p>
                        </div>
                        <div class="more-products-card">
                            <img src='https://i.ibb.co/Nj1rN4G/featured-3-9fa922e9.png' alt="Imágen 3" />
                            <p>$00.000</p>
                        </div>
                        <div class="more-products-card">
                            <img src='https://i.ibb.co/yg3DFHG/featured-1-f6fd0918.png' alt="Imágen 1" />
                            <p>$00.000</p>
                        </div>
                        <div class="more-products-card">
                            <img src='https://i.ibb.co/Nj1rN4G/featured-3-9fa922e9.png' alt="Imágen 3" />
                            <p>$00.000</p>
                        </div>
                        <div class="more-products-card">
                            <img src='https://i.ibb.co/yg3DFHG/featured-1-f6fd0918.png' alt="Imágen 1" />
                            <p>$00.000</p>
                        </div>
                    </div>
                </div>

            </div>
            <div className='parallax'>
            </div>
        </div>

    )
}

export default Main;